/* ---------------------------------------------------------- */
/* print styles */
/* ---------------------------------------------------------- */
@media print {
  * {
    background: transparent !important;
    color: black !important;
    text-shadow: none !important;
    filter: none !important;
    -ms-filter: none !important;
  }
  a, a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  .ir a:after, a[href^="javascript:"]:after, a[href^="#"]:after {
    content: "";
  }
  pre, blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr, img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  @page {
    margin: 0.5cm;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
}

/** STYLES FOR MOBILE DEVICES **/
/* Sytyles for webpage main header */
/* container for background looped video & gradient overlay */
.videoBanner {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.videoBanner::after {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#5857F2, #8F60EB);
  opacity: 0.8;
  position: absolute;
  top: 0;
  left: 0;
}

.videoBanner video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* general grid layout for webapage main header content */
.bannerGrid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: auto 260px auto;
  grid-template-rows: 60px auto 50px;
  grid-template-areas: 'nav nav nav' 'banner banner banner' 'bottomLeft bottomCenter bottomRight';
  color: #FFF;
}

.main-nav {
  grid-area: nav;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

